/**
 * 
 */
enum GSPElementDataType {
    /**
     * 
     */
    String = 'String',

    /**
     * 
     */
    Text = 'Text',

    /**
     * 
     */
    Integer = 'Integer',

    /**
     * 
     */
    Decimal = 'Decimal',

    /**
     * 
     */
    Boolean = 'Boolean',

    /**
     * 
     */
    Date = 'Date',

    /**
     * 
     */
    DateTime = 'DateTime',

    /**
     * 
     */
    Binary = 'Binary',
}

export { GSPElementDataType };


export const GSPElementDataTypes:
    Array<{
        text: string, value: GSPElementDataType | string,
        canChangeLength: boolean, canChangePresicion: boolean, length: number, precision: number
    }> = [
        {
            text: '', value: GSPElementDataType.String, canChangeLength: true,
            canChangePresicion: false, length: 36, precision: 0
        },
        {
            text: '', value: GSPElementDataType.Text, canChangeLength: false,
            canChangePresicion: false, length: 0, precision: 0
        },
        {
            text: '', value: GSPElementDataType.Integer, canChangeLength: false,
            canChangePresicion: false, length: 0, precision: 0
        },
        {
            text: '', value: GSPElementDataType.Decimal, canChangeLength: true,
            canChangePresicion: true, length: 18, precision: 0
        },
        {
            text: '', value: GSPElementDataType.Boolean, canChangeLength: false,
            canChangePresicion: false, length: 1, precision: 0
        },
        {
            text: '', value: GSPElementDataType.Date, canChangeLength: false,
            canChangePresicion: false, length: 0, precision: 0
        },
        {
            text: '', value: GSPElementDataType.DateTime, canChangeLength: false,
            canChangePresicion: false, length: 0, precision: 0
        },
        {
            text: '', value: GSPElementDataType.Binary, canChangeLength: false,
            canChangePresicion: false, length: 0, precision: 0
        },
        {
            text: '', value: 'UnifiedDataType', canChangeLength: true,
            canChangePresicion: true, length: 36, precision: 0
        },
    ];






