import { Component, Input, Output, EventEmitter, OnInit, AfterViewInit, OnChanges } from '@angular/core';

// 
export enum ViewType { dev, ext, show }

@Component({
  selector: 'app-task-flow',
  templateUrl: './task-flow.component.html'
})
export class TaskFlowComponent implements OnInit {
  shownTasks: any = { name: '' };

  @Input() tasks: any[];

  @Input() viewtype: ViewType;

  @Output() taskNodeSelected = new EventEmitter<any>();
  @Output() taskNodeBlur = new EventEmitter<any>();
  @Output() operateTasks = new EventEmitter<any>();

  constructor() {
    // 
    this.viewtype = ViewType.ext;
  }

  ngOnInit() {
    // this._prependStartNode();
  }

  handleTaskSelection(data) {
    this.taskNodeSelected.emit(data);
  }

  handleTaskUnselect(data) {
    this.taskNodeBlur.emit(data);
  }
}



